import { ReactElement, useEffect, useState, useCallback } from 'react'

import { 
	MdCallEnd, MdMic, MdMicOff, MdVideocam,
	MdVideocamOff, MdCall, MdAddCall 
} from 'react-icons/md'
import { 
	TrueIDClient,
	TrueIDCaller
} from 'trueid-client-sdk'

import Camera from './Camera'
import CallingAnimate from './CallingAnimate'
import Audio from './Audio'

//@ts-ignore
import ringingAudio from '../assets/audio/phone-calling.mp3'
//@ts-ignore
import busyAudio from '../assets/audio/phone-busy.mp3'

interface Props {
	localStream: MediaStream | null
}

enum CONVERSATION_STATUS {
	START,
	WAITING,
	IN_CALL,
	END
}
enum CONVERSATION_MODES {
	CALL = 'call',
	JOIN = 'join'
}

const parseURL = () => {
	const url = new URL(window.location.href)
	const roomId = url.searchParams.get('roomId') || ''
	const token = url.searchParams.get('token') || ''
	const mode = url.searchParams.get('mode')
	return {
		roomId,
		token,
		mode
	}
}

const sessionData = parseURL()
const socketURL = 'wss://videocall-stag.trueid.vn/ws2'

const trueIdClient: TrueIDClient = new TrueIDClient()
let trueIdCaller: TrueIDCaller | null = null

export default function MakeCall({ }: Props): ReactElement {
	const [remoteStream, setRemoteStream] = useState<MediaStream | null>(null)
	const [localStream, setLocalStream] = useState<MediaStream | null>(null)
	const [enableMic, setEnableMic] = useState(true)
	const [enableCam, setEnableCam] = useState(true)
	const [conversationStatus, setConversationStatus] = useState(CONVERSATION_STATUS.START)
	const [audioSrc, setAudioSrc] = useState({
		src: '',
		loop: false
	})
	const [socketConnected, setSocketConnected] = useState<boolean>(false)

	const addCallListener = useCallback(() => {
		trueIdCaller?.on('addLocalStream', (stream: MediaStream) => {
			setLocalStream(stream)
		})
		trueIdCaller?.on('addRemoteStream', (stream: MediaStream) => {
			console.log('addRemoteStream', stream)
			setRemoteStream(stream)
		})
		trueIdCaller?.on('ringing', () => {
			setConversationStatus(CONVERSATION_STATUS.WAITING)
			setAudioSrc({
				loop: true,
				src: ringingAudio
			})
		})
		trueIdCaller?.on('accepted', () => {
			setConversationStatus(CONVERSATION_STATUS.IN_CALL)
			setAudioSrc({
				src: '',
				loop: false
			})
			console.log('accepted')
		})
		trueIdCaller?.on('joined', () => {
			setConversationStatus(CONVERSATION_STATUS.IN_CALL)
			setAudioSrc({
				src: '',
				loop: false
			})
		})
		trueIdCaller?.on('ended', () => {
			handleEndCall()
		})

	} , [])
	useEffect(() => {
		if (!sessionData.token) {
			return console.error('Token is required')
		}
		trueIdClient.connect(sessionData.token, 1 , socketURL)
		trueIdClient.on('authen', () => {
			trueIdCaller = new TrueIDCaller(trueIdClient) 
			addCallListener()
			setSocketConnected(true)
			trueIdCaller.getLocalVideoStream()
		})
		
	}, [])

	const handleEndCall = () => {
		setAudioSrc({
			src: '',
			loop: false
		})
		setConversationStatus(CONVERSATION_STATUS.END)
	}

	const makeCall = useCallback(() => {
		trueIdCaller?.makeCall()
	}, [])

	const joinCall = useCallback(() => {
		trueIdCaller?.joinCall(sessionData.roomId)
	}, [])

	const toggleCamera = () => {
		const value = !enableCam
		setEnableCam(value)
		trueIdCaller?.enableLocalVideo(value)
	}
	const toggleMic = () => {
		const value = !enableMic
		setEnableMic(value)
		trueIdCaller?.mute(value)
	}

	const endCall = useCallback(() => {
		if (sessionData.mode === CONVERSATION_MODES.CALL) {
			trueIdCaller?.hangup()
		} else {
			trueIdCaller?.leaveRoom()
		}
		handleEndCall()
	}, [])
	
	return (
		<div className="app-Wrapper">
			{
				conversationStatus !== CONVERSATION_STATUS.END && (
					<>
						<div className="app-Wrapper_Stream">
							<div className={conversationStatus !== CONVERSATION_STATUS.START ? 'app-Wrapper_StreamLocal app-Wrapper_StreamLocal-fixed' : 'app-Wrapper_StreamLocal'}>
								<div className="app-Wrapper_StreamLocalWrapper">
									<Camera srcObject={localStream} muted={true} />
									{
										conversationStatus === CONVERSATION_STATUS.START && (
											<div className="app-Wrapper_StreamLocalWrapperActions">
												<button className="btn-icon mic" disabled={!localStream} onClick={toggleMic}>{ enableMic ? <MdMic /> : <MdMicOff /> }</button>
												<button className="btn-icon cam" disabled={!localStream} onClick={toggleCamera}>{ enableCam ? <MdVideocam /> : <MdVideocamOff />}</button>
												{
													sessionData.mode === CONVERSATION_MODES.CALL && (<button className="btn-icon call" disabled={!socketConnected} onClick={makeCall}><MdCall /></button>)
												}
												{
													sessionData.mode === CONVERSATION_MODES.JOIN && (<button className="btn-icon call" disabled={!socketConnected || !sessionData.roomId} onClick={joinCall}><MdAddCall /></button>)
												}
										</div>
										)
									}
								</div>
							</div>
							{
								conversationStatus !== CONVERSATION_STATUS.START && 
									(
										<div className="app-Wrapper_StreamRemote">
												{
													remoteStream && (<Camera srcObject={remoteStream} />)
												}
												{
													conversationStatus === CONVERSATION_STATUS.WAITING && (
														<div className="app-Wrapper_StreamRemoteWaiting"> 
															<div className="app-Wrapper_StreamRemoteWaitingContent">
																<CallingAnimate />
															</div>
														</div>
													)
												}
			
												<div className="app-Wrapper_StreamLocalWrapperActions">
													<button className="btn-icon mic" onClick={toggleMic}>{ enableMic ? <MdMic /> : <MdMicOff /> }</button>
													<button className="btn-icon cam" onClick={toggleCamera}>{ enableCam ? <MdVideocam /> : <MdVideocamOff />}</button>
													<button className="btn-icon end-call" onClick={endCall}><MdCallEnd /></button>
											</div>
										</div>
									)
							}
						</div>
						<Audio audioSrc={audioSrc.src} loop={audioSrc.loop} />
					</>
				)
			}
			{
				conversationStatus === CONVERSATION_STATUS.END && (
					<div>Call ended</div>
				)
			}
		</div>
	)
}
